.popup{
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px; 
    border-radius: 4px;
    width: 800px;
      height: 650px;
  } 
  .closePopup{
    background: url(../../../img/close.png) no-repeat ;
    width: 12px;
    height: 12px;
    opacity: 0.75;
    cursor: pointer;
    margin-left: 20px;
    position: absolute;
    top: 20px;
    right: 20px;
  }
  .popup img{

      width: 800px;
      height: 650px;
  }