.status:hover{
    background-color: #f0f5f5;
}
.status{
    cursor: pointer;
    display: block;
}
.statusContainer{
    position: relative;
}
.changeStatusContainer{
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fafbfc;
    padding: 15px;
    border: 1px solid #d3d9de;
}
.inputStatus{
    min-width: 300px;
    margin-bottom: 10px;

}
.saveButton{
    width: 50px;
    height: 24px;
    cursor: pointer;
    background: none repeat scroll 0 0 transparent;
    background-color: rgb(17, 6, 49);
    color: #fff;
    border: medium none;
    border-spacing: 0;
    font-family: 'PT Sans Narrow',sans-serif;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.42rem;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    text-align: left;
    text-decoration: none;
    text-indent: 0;
    display: block;
    cursor: pointer;
    margin-left: 0;
    margin-top: 10px;
    text-align: center;
    vertical-align: middle;
    padding-bottom: 10px;
}