.popup{
    margin-left: auto;
    margin-right: auto;
    margin-top: 200px;
    background: white;  
    max-width: 600px;
    min-height: 337px;
    border-radius: 4px;
}
.headerPopup{
    display: flex;
    background-color: #3C5A99;
    color: #fff;
    padding: 20px;
    align-items: center;
}
.popupBody{
    padding: 20px;
    background: #f7f7f7;

}
.popupAvatar{
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.userHeader{
    display: flex;
    margin: 0;
}
.usernameBlock{
    margin-left: 20px;
    padding-top: 10px;
}
.usernameBlock p{
    margin: 0;
    margin-bottom: 5px;
    font-weight: 700;
}
.linkMessage{
    margin-left: auto;
}
.popup a{
    text-decoration: none;
    
}
.headerPopup a{
    color: #fff;
    opacity: 0.75;
    
}
.headerPopup a:active{
    color: #fff;
}
.userHeader a,
.userHeader a:active{
    color: #42648b;
}
.popup a:hover{
    text-decoration: underline;
}
.popupUserName{
    font-size: 20px;
}
.onlineStatus{
    font-size: 16px;
    font-weight: 500 !important;
    color: #939393;
}
.closePopup{
    background: url(../../../img/close.png) no-repeat ;
    width: 12px;
    height: 12px;
    opacity: 0.75;
    cursor: pointer;
    margin-left: 20px;
}
.messageForm{
    padding: 25px;
    padding-top: 0;
}

  
.buttonForm{
    /*Reset's every elements apperance*/
    background: none repeat scroll 0 0 transparent;
    border: medium none;
    border-spacing: 0;
    color: #fff;
    background-color: #3C5A99;
    font-family: 'PT Sans Narrow',sans-serif;
    font-size: 16px;
    font-weight: normal;
    line-height: 35px;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    text-align: left;
    text-decoration: none;
    text-indent: 0;
    display: block;
    width: 80px;
    cursor: pointer;
    border-radius: 4px;
   
    text-align: center;
    margin-left: auto;
}
